section.introduction{height:100vh;background:rgba(245,245,245,.7);padding:2.5em 1.5em;display:flex;justify-content:space-around;align-items:none;flex-direction:row;position:relative}section.introduction div.bg{position:absolute;right:0;bottom:0;width:40%;height:50%;border-radius:50% 50% 0 0;background:#f80}section.introduction section.image-section,section.introduction article.text-section{position:relative;z-index:2}section.introduction section.image-section{width:40%;display:flex;flex-direction:column}section.introduction section.image-section section.redirect{display:flex;justify-content:space-between;align-items:none;flex-direction:row;color:#2d2d2d;padding-bottom:2em;cursor:pointer;transition:.3s;position:relative}section.introduction section.image-section section.redirect p{font-weight:600;letter-spacing:1px;font-size:clamp(1rem, 4vw, 1.25rem)}section.introduction section.image-section section.redirect span{display:block;height:30px;width:30px;transition:1s}section.introduction section.image-section section.redirect::before{content:"";position:absolute;bottom:0;left:50%;translate:-50% 0;height:2px;width:100%;scale:0 1;background:#f80;transition:1s}section.introduction section.image-section section.redirect:hover span{rotate:180deg;color:#f80}section.introduction section.image-section section.redirect:hover::before{scale:1 1}section.introduction section.image-section figure{display:flex;justify-content:flex-end;align-items:center;flex-direction:row;height:100%}section.introduction section.image-section figure img{height:100%}section.introduction article.text-section{width:45%;color:#2d2d2d;display:flex;justify-content:space-around;align-items:none;flex-direction:column}section.introduction article.text-section section.header-icon{display:flex;justify-content:flex-end;align-items:center;flex-direction:row;flex-direction:row-reverse;color:#707070}section.introduction article.text-section section.header-icon span{display:block;height:50px;width:50px;margin-right:.25em;border-radius:.5em;border:2px solid #f80;display:flex;justify-content:center;align-items:center;flex-direction:row;color:#f80}section.introduction article.text-section section.header-icon span svg{width:70%;height:70%}section.introduction article.text-section section.header-icon h3{font-weight:800;font-family:"Arvo",serif;font-size:clamp(0.9rem, 4vw, 1rem)}section.introduction article.text-section section.text-container h2{margin-bottom:.25em;font-size:clamp(2rem, 8vw, 2.75rem);font-weight:800}section.introduction article.text-section section.text-container p{font-size:clamp(0.9rem, 4vw, 1.1rem);border-left:3px solid #f80;padding-left:.55em}section.introduction article.text-section button{padding:1.25em 0;background:#f80;outline:0;border:0;cursor:pointer;transition:1s;border-radius:.25em}section.introduction article.text-section button label{color:#f5f5f5;font-weight:600;font-size:clamp(1rem, 4vw, 1.1rem)}section.introduction article.text-section button:hover{background:#ff4500}@media(max-width: 1000px){section.introduction div.bg{display:none}section.introduction section.image-section figure{justify-content:center}}@media(max-width: 768px){section.introduction{height:auto;min-height:100vh;padding:2.5em .5em;flex-direction:column}section.introduction article.text-section{width:100%;text-align:center}section.introduction article.text-section section.header-icon{margin-bottom:2em}section.introduction button{margin-top:2em}section.introduction section.image-section{width:100%;position:static}section.introduction section.image-section figure{display:none}section.introduction section.image-section section.redirect{padding:0}}