section.location{display:flex;justify-content:center;align-items:center;flex-direction:row;min-height:100vh;overflow:hidden}section.location article.left-section,section.location iframe{width:35%;min-width:450px}section.location article.left-section{margin-right:1em}section.location article.left-section p{text-shadow:0 0 1px #2d2d2d}section.location article.left-section p.header{font-weight:800;font-size:clamp(2rem, 8vw, 3rem)}section.location article.left-section p.text{text-shadow:0 0 2px #2d2d2d;font-weight:500;margin-bottom:2em;font-size:clamp(0.9rem, 4vw, 1.1rem);border-left:3px solid #f80;padding-left:.5em}section.location article.left-section section.details{display:flex;justify-content:space-between;align-items:none;flex-direction:row;translate:0 100vh}section.location article.left-section section.details div{aspect-ratio:1;background:rgba(255,136,0,.9);border-radius:.25em;width:30%;display:flex;justify-content:space-between;align-items:center;flex-direction:column;padding:1.25em .75em;text-align:center}section.location article.left-section section.details div span{width:50%;aspect-ratio:1;display:flex;justify-content:center;align-items:center;flex-direction:row;border:1px solid #f5f5f5;border-radius:50%}section.location article.left-section section.details div span svg{width:70%;height:70%}section.location article.left-section section.details div p{font-weight:700;font-size:clamp(0.9rem, 4vw, 1rem)}section.location iframe{aspect-ratio:1.25}section.location.viewport article.left-section section.details{animation:locationAnim 2.5s;animation-fill-mode:forwards}@keyframes locationAnim{0%{translate:0 100vh}100%{translate:0}}@media(max-width: 1000px){section.location{flex-direction:column;padding:2em 0}section.location article.left-section{width:100%;margin-bottom:2em;margin-right:0;min-width:auto}section.location article.left-section p.header{padding:0 .5em}section.location article.left-section p.text{padding:0 1.25em;border:0}section.location article.left-section section.details{padding:0 .25em}section.location article.left-section section.details div{width:33%}}@media(max-width: 480px){section.location iframe{min-width:auto;max-width:none;width:98%}}