section.slider-section article.text-container{width:46%;translate:100vw 0}section.slider-section article.text-container p.header{font-weight:800;font-size:clamp(2rem, 10vw, 3.5rem);color:#f80;text-shadow:0 0 2px #2d2d2d}section.slider-section article.text-container p.text{color:#2d2d2d;font-size:clamp(0.95rem, 4vw, 1.1rem);margin:.75em 0 2em 0;border-left:3px solid #3155be;padding-left:.5em;text-shadow:0 0 1px #f5f5f5}section.slider-section article.text-container button{background:#3155be;cursor:pointer;outline:0;border:0;transition:.5s;border-radius:2em}section.slider-section article.text-container button label{color:#f5f5f5;padding:1em 2em;font-size:clamp(0.9rem, 4vw, 1rem);font-weight:500;display:block}section.slider-section article.text-container button:hover{border-radius:.25em;background:#4169e1}@media(max-width: 768px){section.slider-section article.text-container{width:100%;text-align:center;margin-bottom:4em}section.slider-section article.text-container p.text{border:0}}