section.reviews{min-height:100vh;position:relative;background:rgba(245,245,245,.7);padding:1.5em;color:#2d2d2d;overflow:hidden}section.reviews section.reviews-container,section.reviews section.text{width:90%;margin:0 auto}section.reviews section.reviews-container{display:flex;justify-content:space-around;align-items:none;flex-direction:row;flex-wrap:wrap;z-index:2;position:relative}section.reviews section.reviews-container article.review{width:45%;min-width:350px;margin:0 .25em 2em .25em;padding:1.5em;border-radius:.5em;background:#f5f5f5}section.reviews section.reviews-container article.review section.review-socials{display:flex}section.reviews section.reviews-container article.review section.review-socials span{display:block;height:40px;width:40px;margin-right:.5em;cursor:pointer;transition:.5s}section.reviews section.reviews-container article.review section.review-socials span:hover{scale:1.2}section.reviews section.reviews-container article.review section.user{display:flex;justify-content:none;align-items:center;flex-direction:row}section.reviews section.reviews-container article.review section.user figure{width:10%;min-width:45px;aspect-ratio:1}section.reviews section.reviews-container article.review section.user figure img{width:100%;height:100%;object-fit:cover}section.reviews section.reviews-container article.review section.user p{font-weight:600;letter-spacing:-1px;margin-left:.5em}section.reviews section.reviews-container article.review section.text-container{margin:2em 0;padding:1em;border-radius:.25em;position:relative}section.reviews section.reviews-container article.review section.text-container span{position:absolute;display:block;height:30px;width:30px;left:0;top:0;translate:-50% -50%}section.reviews section.reviews-container article.review section.text-container p{font-size:clamp(0.9rem, 3vw, 0.95rem)}section.reviews section.text{margin-bottom:4em}section.reviews section.text p.header{font-weight:800;font-size:clamp(2rem, 9vw, 2.5rem);color:#f80;text-shadow:0 0 1px #2d2d2d}section.reviews section.text p.text{font-family:"Arvo",serif;font-size:clamp(0.9rem, 4vw, 1.1rem)}section.reviews div.bg{position:absolute;height:100vh;width:30%;background:#f80}section.reviews div.bg.one{top:0;left:25%;translate:-25% 0;transform:skew(-45deg)}section.reviews div.bg.two{top:100vh;left:0;width:21%}@media(max-width: 1000px){section.reviews div.bg{display:none}}@media(max-width: 480px){section.reviews{padding:1.5em .5em}section.reviews section.reviews-container,section.reviews section.text{width:100%}section.reviews section.reviews-container article.review{min-width:auto;max-width:none;width:100%}}