article.content-card{display:flex;justify-content:center;align-items:center;flex-direction:column;width:100%;height:100%}article.content-card div.header{margin-bottom:.5em;font-size:clamp(1rem, 6vw, 1.5rem);background:#f80;padding:.5em 3em;position:relative;color:#f5f5f5;font-weight:700}article.content-card div.header::after,article.content-card div.header::before{content:"";height:50%;background:#f5f5f5;aspect-ratio:1;position:absolute;top:50%;rotate:45deg}article.content-card div.header::before{right:0;translate:50% -50%}article.content-card div.header::after{left:0;translate:-50% -50%}article.content-card div.card{width:50%;min-width:300px;max-width:350px;border:2px solid #f80;border-radius:.25em;position:relative;aspect-ratio:.75;color:#f5f5f5;display:flex;justify-content:flex-end;align-items:flex-start;flex-direction:column;padding:1em}article.content-card div.card p{text-shadow:0 0 2px #2d2d2d}article.content-card div.card p.name{font-weight:700;border-left:3px solid #f80;padding-left:.5em;font-size:clamp(1rem, 5vw, 1.25rem)}article.content-card div.card p.price{font-weight:800;margin:.25em 0;font-family:"Arvo",serif;letter-spacing:2px;font-size:2.75rem;display:flex;justify-content:none;align-items:flex-start;flex-direction:row}article.content-card div.card p.price span{font-size:1.5rem}article.content-card div.card a{padding:.75em 2em;background:#f80;outline:0;border:0;border-radius:.25em;cursor:pointer;transition:.3s;text-decoration:none;color:#f5f5f5;font-weight:600}article.content-card div.card a:hover{background:orange}article.content-card div.card figure{width:100%;height:100%;position:absolute;left:0;top:0;z-index:-1}article.content-card div.card figure img{width:100%;height:100%;object-fit:cover}@media(max-width: 480px){article.content-card div.card{min-width:auto;max-width:none;width:90%;max-width:300px}}