section.dropdowns{display:flex;justify-content:space-around;align-items:center;flex-direction:row;min-height:100vh;background:rgba(245,245,245,.7);position:relative}section.dropdowns section.dropdown-container,section.dropdowns section.content{position:relative;z-index:2;min-height:650px;width:45%;aspect-ratio:1}section.dropdowns section.dropdown-container{background:#f5f5f5;color:#2d2d2d;padding:1em;display:flex;justify-content:space-around;align-items:none;flex-direction:column;translate:-100vw 0}section.dropdowns section.dropdown-container section.option section.header{display:flex;justify-content:space-between;align-items:center;flex-direction:row;cursor:pointer;margin-bottom:.25em}section.dropdowns section.dropdown-container section.option section.header div{display:flex;justify-content:space-between;align-items:center;flex-direction:row;width:80%}section.dropdowns section.dropdown-container section.option section.header p.topic{background:#f80;padding:.5em;color:#f5f5f5;font-weight:700;border-radius:.5em;pointer-events:none;font-size:clamp(0.9rem, 4vw, 1rem)}section.dropdowns section.dropdown-container section.option section.header p.header{font-weight:600;margin-left:.25em;font-size:clamp(0.95rem, 4vw, 1.1rem)}section.dropdowns section.dropdown-container section.option section.header span{display:block;height:30px;width:30px;transition:.3s}section.dropdowns section.dropdown-container section.option section.expanded-menu{width:80%;margin:0 0 0 auto;height:0;overflow:hidden;display:none;transition:.3s}section.dropdowns section.dropdown-container section.option section.expanded-menu p{font-size:clamp(0.9rem, 4vw, 1rem)}section.dropdowns section.content{padding:1em;background:#f5f5f5;color:#2d2d2d}section.dropdowns div.bg{position:absolute;top:0;background:#f80;width:30%;height:100vh;left:25%;translate:-25% 0;transform:skew(-45deg)}section.dropdowns.viewport section.dropdown-container{animation:ddAnim 2s;animation-fill-mode:forwards}@keyframes ddAnim{0%{translate:-100vw 0}100%{translate:0 0}}@media(max-width: 1000px){section.dropdowns{flex-direction:column;padding:.5em;justify-content:center}section.dropdowns div.bg{display:none}section.dropdowns section.dropdown-container section.option{margin:1em 0}section.dropdowns section.dropdown-container,section.dropdowns section.content{min-height:auto;width:100%;aspect-ratio:auto}}@media(max-width: 480px){section.dropdowns section.content{padding:.25em}section.dropdowns section.dropdown-container{padding:.5em}section.dropdowns section.dropdown-container section.option section.expanded-menu{width:100%}}