article.content-blog{display:flex;justify-content:space-between;align-items:none;flex-direction:column;width:100%;height:100%}article.content-blog figure{width:100%;aspect-ratio:2.5}article.content-blog figure img{width:100%;height:100%;object-fit:cover}article.content-blog section.paragraphs p.title{font-weight:700;font-size:clamp(1.5rem, 8vw, 2.75rem)}article.content-blog section.paragraphs p.text{font-weight:500;font-size:1.1rem}article.content-blog section.informations{display:flex;justify-content:space-between;align-items:center;flex-direction:row}article.content-blog section.informations p.category{color:#b4b4b4;font-weight:600}article.content-blog section.informations section.tags{display:flex}article.content-blog section.informations section.tags span{margin-right:.5em;padding:.5em;border-radius:.25em;background:#f80;color:#f5f5f5;font-weight:600;font-size:clamp(0.85rem, 3vw, 0.95rem)}article.content-blog a{padding:.75em 2em;background:#f80;border-radius:.25em;border:0;outline:0;width:max-content;cursor:pointer;background:#3155be;transition:.3s;color:#f5f5f5;font-weight:500;text-decoration:none;font-size:clamp(0.9rem, 4vw, 1rem)}article.content-blog a:hover{background:#4169e1}@media(max-width: 1000px){article.content-blog section.informations section.tags{margin-bottom:.5em}article.content-blog a{margin-top:2em}}@media(max-width: 480px){article.content-blog section.informations{flex-direction:column;align-items:flex-start}}