main.blog-page{min-height:100vh;margin-top:70px;background:rgba(245,245,245,.7);padding:2em}main.blog-page section.container{margin-top:3em;display:flex;justify-content:space-around;align-items:center;flex-direction:row;align-items:stretch;flex-wrap:wrap}main.blog-page section.container article{margin:.75em;width:45%;min-width:480px;padding:1.25em;border-radius:.25em;color:#2d2d2d;background:#f5f5f5;margin-bottom:2em;position:relative;overflow:hidden;color:#f5f5f5;text-shadow:0 0 2px #2d2d2d}main.blog-page section.container article section.tags{position:relative;z-index:2;display:flex}main.blog-page section.container article section.tags span{margin-left:.5em;background:#3155be;font-size:.9rem;font-weight:500;padding:.5em;border-radius:.25em}main.blog-page section.container article p.category{position:absolute;right:.75em;bottom:.75em;color:#b4b4b4;text-shadow:0 0 2px #2d2d2d;font-size:.9rem;z-index:2;font-weight:800}main.blog-page section.container article div.wrap{position:relative;z-index:2;display:flex;justify-content:space-between;align-items:flex-start;flex-direction:column;width:100%;height:100%}main.blog-page section.container article div.wrap p.header{font-weight:800;margin:1em 0;font-size:clamp(1.75rem, 5vw, 2.25rem)}main.blog-page section.container article div.wrap p.text{font-weight:500;margin:.25em 0 .75em 0}main.blog-page section.container article a{text-decoration:none;padding:.75em 2em;outline:0;border:0;background:#3155be;transition:.3s;cursor:pointer;border-radius:.25em;color:#f5f5f5;font-weight:500;font-size:clamp(0.9rem, 4vw, 1rem)}main.blog-page section.container article a:hover{background:#4169e1}main.blog-page section.container article figure{position:absolute;left:0;top:0;width:100%;height:100%}main.blog-page section.container article figure img{width:100%;height:100%;object-fit:cover}main.blog-page section.searchbar{display:flex;justify-content:space-between;align-items:center;flex-direction:row}main.blog-page section.searchbar h1{text-shadow:0 0 2px #2d2d2d;font-size:clamp(1.75rem, 8vw, 2.25rem);font-weight:800}main.blog-page section.searchbar div{position:relative;width:40%}main.blog-page section.searchbar div input{width:100%;padding:.75em;border-radius:.2em;font-size:clamp(0.9rem, 4vw, 1rem);outline:0;border:1px solid #b4b4b4;padding-right:40px}main.blog-page section.searchbar div span{position:absolute;right:8px;top:50%;translate:0 -50%;display:block;height:30px;width:30px;color:#2d2d2d}@media(max-width: 768px){main.blog-page{padding:2em .5em}main.blog-page section.container article{aspect-ratio:none;min-width:auto;width:100%;margin:.75em 0;padding:.75em}}@media(max-width: 480px){main.blog-page section.container article p.category{position:relative;z-index:2;right:0;bottom:0;margin-top:1em;text-align:right;width:100%}main.blog-page section.searchbar div{width:60%}}