section.slider-section{min-height:100vh;display:flex;justify-content:space-around;align-items:center;flex-direction:row;padding:.5em;background:rgba(245,245,245,.7);position:relative;overflow:hidden}section.slider-section section.slider-container,section.slider-section article.text-container{position:relative;z-index:2}section.slider-section div.bg{position:absolute;height:100%;width:30%;background:#f80;bottom:0;left:25%;translate:-25% 0;transform:skew(45deg)}section.slider-section.viewport article.text-container{animation:sliderArt 2s;animation-fill-mode:forwards}@keyframes sliderArt{0%{translate:100vw 0}100%{translate:0 0}}@media(max-width: 1000px){section.slider-section div.bg{display:none}}@media(max-width: 768px){section.slider-section{flex-direction:column-reverse;padding:5em .5em}}