article.content-text{width:100%;height:100%;display:flex;justify-content:center;align-items:none;flex-direction:column;padding:2em;position:relative;color:#f5f5f5}article.content-text video{position:absolute;left:0;top:0;width:100%;height:100%;object-fit:cover;z-index:-1}article.content-text p.header{font-weight:800;font-size:clamp(2rem, 8vw, 3rem)}article.content-text p.text{margin:.25em 0 1em 0;font-size:clamp(0.9rem, 4vw, 1rem)}article.content-text a{width:max-content;text-decoration:none;padding:1em 2em;background:#ff4500;cursor:pointer;font-size:clamp(0.9rem, 4vw, 1rem);transition:.3s;border-radius:.25em;color:#f5f5f5;font-weight:600}article.content-text a:hover{background:#f80}@media(max-width: 480px){article.content-text{padding:1em}}