header.homepage-header{padding-top:70px;height:100vh;background-image:linear-gradient(rgba(30, 30, 30, 0.3), rgba(30, 30, 30, 0.6)),url("../images/header.jpg");background-size:cover;display:flex;justify-content:center;align-items:center;flex-direction:column}header.homepage-header h1{font-family:"Arvo",serif;font-weight:800;width:80%;font-size:clamp(2.5rem, 10vw, 4.5rem);text-align:center;text-shadow:0 0 5px #ff4500;translate:-100vw 0;animation:anim1 2s;animation-fill-mode:forwards}@keyframes anim1{0%{translate:-100vw 0}100%{translate:0 0}}header.homepage-header p{font-size:clamp(0.9rem, 4.5vw, 1.25rem);width:50%;text-align:center;margin:1em 0;animation:anim2 2s;animation-fill-mode:forwards;translate:100vw 0}@keyframes anim2{0%{translate:100vw 0}100%{translate:0 0}}header.homepage-header button{border-radius:1em;background:rgba(0,0,0,0);border:2px solid orange;outline:0;cursor:pointer;transition:.5s;opacity:0;animation:anim3 4s;animation-fill-mode:forwards}header.homepage-header button label{font-weight:500;color:orange;font-size:clamp(0.9rem, 4vw, 1.1rem);transition:.5s;padding:1.25em 2em;display:block}header.homepage-header button:hover{background:#ff4500;border-color:#ff4500}header.homepage-header button:hover label{color:#f5f5f5}@keyframes anim3{0%{opacity:0}100%{opacity:1}}@media(max-width: 480px){header.homepage-header{padding:0 .5em}header.homepage-header h1,header.homepage-header p{width:100%}}