section.tiles{min-height:100vh;display:flex;justify-content:space-around;align-items:none;flex-direction:column;padding:2em 0;width:90%;margin:0 auto}section.tiles div.text-header{overflow:hidden;margin-bottom:2em}section.tiles div.text-header p.section-header{text-shadow:0 0 4px #2d2d2d;font-weight:800;font-family:"Arvo",serif;position:relative;font-size:clamp(1.75rem, 6vw, 2rem);z-index:-1;width:max-content;max-width:100%;text-align:center;padding-right:1em}section.tiles div.text-header p.section-header::before{content:"";width:100vw;height:4px;background:#f5f5f5;position:absolute;top:50%;left:100%;translate:0 -50%}section.tiles section.container{display:flex;justify-content:center;align-items:center;flex-direction:row;align-items:stretch;flex-wrap:wrap}section.tiles section.container.viewport article.tile:nth-child(1){animation:tileAnim1 2s;animation-fill-mode:forwards}section.tiles section.container.viewport article.tile:nth-child(3){animation:tileAnim2 3s;animation-fill-mode:forwards}@keyframes tileAnim1{0%{translate:100vw 0}100%{translate:0}}@keyframes tileAnim2{0%{translate:-100vw 0}100%{translate:0}}section.tiles article.tile{display:flex;justify-content:center;align-items:center;flex-direction:column;width:24%;min-width:350px;max-width:500px;position:relative;margin:.75em}section.tiles article.tile div.wrap{width:100%;height:100%;display:flex;justify-content:center;align-items:center;flex-direction:column;position:relative;border-radius:1em;z-index:4;background:#252525;padding:4em 2em}section.tiles article.tile::after,section.tiles article.tile::before{content:"";position:absolute;border-radius:1em;width:20%;aspect-ratio:1;z-index:-1;background:#3155be;box-shadow:0 0 4px #6da0ff,0 0 8px #6da0ff,0 0 16px #6da0ff}section.tiles article.tile::before{left:-5px;top:-5px}section.tiles article.tile::after{right:-5px;bottom:-5px}section.tiles article.tile button{margin-top:2em;border-radius:.25em;width:100%;padding:1em 0;outline:0;border:2px solid #f80;background:#f80;cursor:pointer;transition:.5s}section.tiles article.tile button label{color:#f5f5f5;font-size:1rem;font-weight:500;text-shadow:0 0 4px #2d2d2d}section.tiles article.tile button:hover{background:rgba(0,0,0,0)}section.tiles article.tile span{width:20%;aspect-ratio:1;color:#f80;margin-bottom:1em}section.tiles article.tile p.header{text-transform:uppercase;font-weight:800;font-size:1.1rem;margin-bottom:1em}section.tiles article.tile p.text{text-align:center;font-size:.9rem;color:#b4b4b4;line-height:1.5em;opacity:.8}section.tiles article.tile:nth-child(1){translate:100vw 0}section.tiles article.tile:nth-child(3){translate:-100vw 0}@media(max-width: 480px){section.tiles{width:100%}section.tiles article.tile{min-width:auto;max-width:none;width:100%}section.tiles article.tile div.wrap{padding:2em .75em}}